import React, {useState, useEffect} from 'react'
import postsService from './service/posts'

function App() {
  const [posts, setPosts] = useState([])
  const effectHandler = () => {
    postsService
      .getAll()
      .then(response => setPosts(response))
      .catch(error => console.log(error))
  }
  useEffect(effectHandler, [])

  const postList = () => (
    <ul>
      {posts.map(p => (
        <li>{p.title}</li>
      ))}
    </ul>
  )

  return (
    <div>
      <h1>Hello World</h1>
      <h2>Posts</h2>
      {postList()}
    </div>
  );
}

export default App;
